/*******************
 01 TYPOGRAPHY START
********************/
body{
	font-size:16px;
	font-family: $font-rubik;
	color: $black;   
	background-color: $white;	
	scroll-behavior: smooth;
}
a{
  text-decoration: none;
  color: $theme-default;
  &:hover{
	color: $theme-default;
  }
}
h1,h2,h3,h4,h5,h6,p{
	margin-block-end: 0;
}
ol, ul, dl{
	margin: 0;
	padding: 0;
}
li{
	list-style: none;
}
p{
	color: $font-light;
	font-weight: 400;
}
.space-py-60{
	padding-top:60px;
	padding-bottom:60px;
}
.space-pt-60{
	padding-top: 60px;
}
.space-pb-60{
	padding-bottom: 60px;
}

/***********************
 14 DOCUMANTATION START
************************/
.dock-header{
	background-color: $theme-default;	
	padding:25px 0;
	z-index:2;
	position: relative;
	.header-contain{
		display: flex;
		align-items: center;
		justify-content: space-between;
		ul{
			display: flex;
			align-items: center;
			li{
				a{
					text-transform: capitalize;
					font-size: 18px;
					color: $white;
					font-weight: 500;
				}
				+li{
					margin-left: 25px;
				}
			}
		}
		.menu-action{
			i{
				font-size:24px;
				color:$white;
			}
		}
		.btn{
			color: $theme-default;
			font-weight: 500;
    		padding: 10px 30px;
			background-color:$white;
		}
	}
	&.sticky{
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
	}
}
.custom-container{
    width: calc(100% - 320px);
    margin-left: auto;
    padding-left: 50px;
    padding-right: 50px;
}
.cdx-sidebar{
	position: fixed;
	top: 0;
	left: 0;
	width: 320px;
	height: 100vh;
	background-color: $white;
	z-index: 3;
	box-shadow: 0 0 20px 0 rgb($black,10%);
	.codex-brand{
		padding:23px 30px;	
		border-bottom: 1px solid $theme-border;
		img{
			width: 210px;
			height: auto;
		}
	}
	.menu-list{
		margin-top:40px;		
		padding-left: 30px;
		padding-right: 30px;
		li{
			text-transform:capitalize;			
			a{			
				color: $theme-default;
				display: flex;
				align-items: center;
				padding:10px 15px;
				background-color: rgba($theme-default,0.1);		
				border-radius:5px;				
				&.active{
					background-color: $theme-default;
					color: $white;
				}
			}
			+li{
				margin-top:10px;
			}
			
		}
	}	
}
.dock-body{
	.telink{
		color:$theme-default !important;
		font-weight:500;
	}
	.dock-title{
		text-transform: uppercase;		
		padding: 20px;
		border-radius: 10px;
		background-color: $black;
		color: $white;
		h4{
			font-size:20px;
		}
	}
	
	.featuer-list{
		padding-left:20px;
		li{
			list-style: square;
			font-size: 16px;
			color: $font-light;
			text-transform: capitalize;
			a{
				color: $font-light;
			}
			+li{
				margin-top:10px;
			}
		}
	}
	
	.folder-list{
		li{
			a{
				display: flex;
				align-items: center;						
				i{  
					color:$theme-default;
					margin-right:10px;
					min-width:20px;
				}
			}
			ul{
				margin-left: 25px;
				li{
					a{
						font-size:18px;
					}
				}
			}        
		}
	}
	
	.wtree {
		position: relative;    
		li {
			position: relative;
			list-style-type: none;
			margin:10px 0 10px 10px;        
			&:before {
				content: " ";
				position: absolute;
				top: 0px;
				left: -25px;
				border-left: 1px solid $font-light;
				border-bottom: 1px solid $font-light;
				width: 15px;
				height: 15px;
			}
			&:after {
				position:absolute;
				content:" ";
				top: 14px;
				left: -25px;
				border-left: 1px solid $font-light;            
				width:15px;
				height:100%;
			}
			&:last-child:after {
				display:none;
			}        
		}
	}
	.card{
		border:none;
		background-color: transparent;	
		.card-body{
			margin-top:30px;
			background-color: $white;
			border-radius:10px;		
			padding:30px;
			border: 1px solid $theme-border;
		}
	}
}




@media screen and (max-width: 991px){
	.cdx-layer{
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba($black,0.8);
		z-index:1;
		opacity: 0;
		visibility: hidden;
		transition: all 0.5s ease;
		&.active{
			opacity: 1;
			visibility: visible;
		}
	}
	.dock-header{
        padding-left: 0;
        padding-right: 0;
		.header-contain {
			.btn{
				margin-right:20px;
				padding:8px 20px;
			}
		}
    }
    .cdx-sidebar{
        inset-inline-start: -320px;
        transition: all 0.5s ease;
        &.active{
            inset-inline-start: 0;
        }
    }
    .custom-container{
        width: 100%;
        padding-left: 24px;
        padding-right: 24px;
    }
}